.formTextArea {
    background-color: #ececec;
    border-radius: 1em;
    border-color: #ececec;
    min-height: 40px;
}
.modalBody {
    border: 0;
}
.modal {
    backdrop-filter: blur(5px);
}
.modalDialog div {
    background: white;
    border-radius: 1em;
    border-color: white;
}
.modalFooter {
    border: 0;
}